<template>
  <div class="main-feed">
    <div v-if="isLoading">
      <LoadingSpinner v-if="isLoading" />
    </div>
    <div v-else class="form-container">
      <h3></h3>

      <div class="radio-group">
        <div class="radio-item">
          <input type="radio" :value="1" v-model="user_mode" />
            .
        </div>
        <div class="radio-item">
          <input type="radio" :value="2" v-model="user_mode" />
             .
        </div>
      </div>

      <div v-if="user_mode === 2">
        <!--   input -->
        <input
          type="file"
          ref="fileInput"
          @change="onFileChange"
          style="display: none"
        />
        <div class="preview-container">
          <img
            v-if="previewUrl"
            :src="previewUrl"
            alt=" "
            class="preview-image"
          />
          <div v-else class="placeholder-box"></div>
        </div>
        <!-- SqureButton   -->
        <SqureButton
          label="   "
          :onClick="triggerFileInput"
        />

        <div v-if="fileName" class="selected-file">
           : {{ fileName }}
        </div>
      </div>

      <!--  form-container  -->
      <div class="form-container">
        <div class="input-label"> </div>
        <InputBox v-model="user_id" placeholder="" />
        <InputBox v-model="user_pass" type="password" placeholder="" />

        <!--   -->
        <template v-if="user_mode === 1">
          <div class="input-label"> </div>
          <InputBox v-model="user_name" placeholder=" ( )" />
          <InputBox v-model="user_number" placeholder=" " />
        </template>

        <!--   -->
        <template v-else>
          <div class="input-label"> </div>
          <InputBox v-model="user_name" placeholder=" " />
          <InputBox v-model="user_number" placeholder=" " />
          <InputBox v-model="store_address" placeholder="" />
          <TextBox
            style="margin: 20px 0px"
            v-model="store_desc"
            placeholder="     :)"
          />
        </template>
      </div>
      <RoundButton
        style="margin: 20px 0px"
        label=""
        :onClick="signUp"
      />
    </div>
  </div>
</template>

<script>
import InputBox from "./InputBox.vue";
import RoundButton from "./RoundButton.vue";
import SqureButton from "./SqureButton.vue";
import LoadingSpinner from "./LoadingSpinner.vue";
import TextBox from "./TextBox.vue";
import { ref, computed } from "vue";
import { useRouter } from "vue-router";
import axios from "axios";

export default {
  name: "AuthFeed",
  components: {
    InputBox,
    RoundButton,
    LoadingSpinner,
    SqureButton,
    TextBox,
  },
  setup() {
    const router = useRouter();

    const isLoading = ref(false);
    const user_mode = ref(1); // : 
    const user_id = ref("");
    const user_pass = ref("");
    const user_name = ref("");
    const user_number = ref("");
    const store_address = ref("");
    const store_desc = ref("");

    const signUp = async () => {
      isLoading.value = true;
      if (user_mode.value == 1) {
        console.log("  ");
        if (
          !user_id.value ||
          !user_pass.value ||
          !user_name.value ||
          !user_number.value
        ) {
          alert("   .");
          isLoading.value = false;
          return;
        }

        const body = {
          user_id: user_id.value,
          user_pass: user_pass.value,
          user_name: user_name.value,
          user_mode: user_mode.value,
          user_number: user_number.value,
        };
        axios
          .post("/api/auth/signup/user", body)

          .then((res) => {
            console.log(res);
            if (res.data === "OK") {
              isLoading.value = false;
              alert(" ");
              isLoading.value = false;
              router.push(`/auth`);
            } else if (res.data === "NOT") {
              alert("  ");
              isLoading.value = false;
            } else if (res.data === "ALREADY") {
              alert("  .");
              isLoading.value = false;
            }
          })
          .catch((error) => {
            alert(" ");
            console.error("Error during login:", error);

            isLoading.value = false;
          });

        isLoading.value = false;
      } else {
        console.log("  ");
        if (
          !user_id.value ||
          !user_pass.value ||
          !user_name.value ||
          !user_number.value ||
          !selectedFile.value ||
          !store_address.value
        ) {
          alert("   .");
          isLoading.value = false;
          return;
        }

        const formData = new FormData();

        formData.append("user_id", user_id.value);
        formData.append("user_pass", user_pass.value);
        formData.append("user_name", user_name.value);
        formData.append("user_mode", user_mode.value);
        formData.append("user_number", user_number.value);
        formData.append("store_image", selectedFile.value);
        formData.append("store_address", store_address.value);
        formData.append("store_desc", store_desc.value);

        axios
          .post("/api/auth/signup/store", formData, {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          })

          .then((res) => {
            console.log(res);
            if (res.data === "OK") {
              isLoading.value = false;
              alert(" ");
              // imageUrl.value = "http://localhost:8000" + res.data;
              isLoading.value = false;
              router.push(`/auth`);
            } else if (res.data === "NOT") {
              alert("  ");
              isLoading.value = false;
            } else if (res.data === "ALREADY") {
              alert("  .");
              isLoading.value = false;
            }
          })

          //    error .
          .catch((error) => {
            if (error.response.status === 413) {
              alert("    .");
            } else {
              alert(" ");
            }
            console.error("Error during login:", error);

            isLoading.value = false;
          });
      }
    };

    const checked = ref(false);

    const handleChange = () => {
      checked.value = true;
    };

    const fileInput = ref(null);
    const selectedFile = ref(null);
    const fileName = ref("");
    const previewUrl = ref("");

    const isFileSelected = computed(() => !!selectedFile.value);
    const triggerFileInput = () => {
      fileInput.value?.click();
    };

    const onFileChange = (event) => {
      const file = event.target.files[0];
      if (file) {
        selectedFile.value = file;
        fileName.value = file.name;
        previewUrl.value = URL.createObjectURL(file);
      }
    };

    return {
      router,
      signUp,
      checked,
      handleChange,
      user_mode,
      user_id,
      user_pass,
      user_name,
      user_number,
      store_address,
      store_desc,
      isLoading,
      fileInput,
      selectedFile,
      fileName,
      previewUrl,
      onFileChange,
      isFileSelected,
      triggerFileInput,
    };
  },
};
</script>

