<template>
  <div v-if="isLoading">
    <LoadingSpinner v-if="isLoading" />
  </div>
  <div v-else id="myGrid" class="ag-theme-mycustomtheme">
    <AgGridVue
      class="ag-theme-balham"
      style="height: 200px; width: 100%"
      :columnDefs="props.columnDefs"
      :rowData="rowData"
      :defaultColDef="defaultColDef"
      rowSelection="single"
      @grid-ready="onGridReady"
      @selectionChanged="onRowSelected"
      :key="gridKey"
    />

    <div
      class="cancel-section"
      style="margin-top: 16px; display: flex; align-items: center"
    >
      <InputBox
        placeholder=" "
        v-model="selectedOrderId"
        style="padding: 4px 8px; width: 200px; margin-right: 20px"
      />
      <div class="toggle-box">
        <button
          v-for="option in statusOptions"
          :key="option"
          :class="['toggle-button', { active: selectedStatus === option }]"
          @click="selectedStatus = option"
        >
          {{ option }}
        </button>
      </div>
      <div v-if="isLoading1">
        <LoadingSpinner v-if="isLoading" />
      </div>
      <SqureButton v-else label=" " :onClick="updateOrder" />
    </div>

    <div
      v-if="selectedStatus === ''"
      class="cancel-section"
      style="margin-top: 16px; display: flex; align-items: center"
    >
      <InputBox
        placeholder=""
        v-model="cake_price"
        style="padding: 4px 8px; width: 200px; margin-right: 20px"
      />
      <InputBox
        placeholder=" "
        v-model="bank_account"
        style="padding: 4px 8px; width: 200px; margin-right: 20px"
      />
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
import LoadingSpinner from "./LoadingSpinner.vue";
import InputBox from "./InputBox.vue";
import SqureButton from "./SqureButton.vue";
import { AgGridVue } from "ag-grid-vue3";

import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-balham.css";
import axios from "axios";
export default {
  components: { AgGridVue, LoadingSpinner, InputBox, SqureButton },
  props: {
    fetchFn: {
      type: Function,
      required: true,
    },
    columnDefs: {
      type: Array,
      required: true,
    },
  },
  setup(props) {
    const isLoading = ref(false);
    const isLoading1 = ref(false);
    const rowData = ref([]);
    const selectedOrderId = ref("");
    const selectedOrderState = ref("");
    const cake_price = ref("");
    const bank_account = ref("");
    const defaultColDef = {
      resizable: true,
      sortable: true,
      filter: true,
    };

    onMounted(async () => {
      isLoading.value = true;
      try {
        const data = await props.fetchFn();

        if (Array.isArray(data)) {
          // fetchFn()   
          rowData.value = data.map((item) => ({ ...item, cancel: false }));
        } else {
          rowData.value = [];
          console.warn("  :", data);
        }

        isLoading.value = false;
      } catch (error) {
        console.error("CustomGrid fetch :", error);
        isLoading.value = false;
      }
    });

    const gridKey = ref(0);
    const columnApi = ref(null);
    const onGridReady = (params) => {
      columnApi.value = params.columnApi;
      //       
      const allColumnIds = [];
      columnApi.value.getAllColumns().forEach((column) => {
        allColumnIds.push(column.getId());
      });
      columnApi.value.autoSizeColumns(allColumnIds, false);
    };

    const onRowSelected = (event) => {
      const rows = event.api.getSelectedRows();
      console.log(rows);
      selectedOrderId.value = rows[0].id;
      selectedOrderState.value = rows[0].order_state;
    };

    const updateOrder = async () => {
      console.log(selectedOrderId.value);
      if (!selectedOrderId.value) {
        return;
      }

      if (
        selectedOrderState.value === "" ||
        selectedOrderState.value === " " ||
        selectedOrderState.value === ""
      ) {
        alert("   .");
        selectedOrderId.value = "";
        selectedOrderState.value = "";
        return;
      }

      if (
        selectedOrderState.value === "" &&
        (selectedStatus.value === " " ||
          selectedStatus.value === "")
      ) {
        console.log("");
        alert("      .");
        return;
      }

      if (
        selectedOrderState.value === "" &&
        (selectedStatus.value === " " ||
          selectedStatus.value === "" ||
          selectedStatus.value === "")
      ) {
        console.log("");
        alert("       .");
        return;
      }
      if (selectedStatus.value === "") {
        if (!cake_price.value || !bank_account.value) {
          console.log("");
          alert(" !");
          return;
        }
      }

      if (
        selectedOrderState.value === "" &&
        selectedStatus.value !== ""
      ) {
        console.log("");
        alert("    .");
        return;
      }

      if (
        selectedOrderState.value === "" &&
        (selectedStatus.value === " " ||
          selectedStatus.value === "")
      ) {
        console.log("");
        alert("      .");
        return;
      }

      const payload = {
        order_id: selectedOrderId.value,
        order_state: selectedStatus.value,
        cake_price: cake_price.value,
        bank_account: bank_account.value,
      };
      try {
        isLoading1.value = true;
        console.log(payload);
        const res = await axios.post("/api/order/update/store", payload);
        if (res.data === "UPDATEOK") {
          alert(" .");
        } else {
          alert(" ");
          console.log(res.data);
        }
      } catch (e) {
        console.error(e);
        alert("  ");
      } finally {
        isLoading1.value = false;
      }
      await reloadData();
      selectedOrderId.value = "";
      cake_price.value = "";
      bank_account.value = "";
    };

    const reloadData = async () => {
      isLoading.value = true;
      try {
        const data = await props.fetchFn();
        if (Array.isArray(data)) {
          rowData.value = data.map((item) => ({ ...item, cancel: false }));
        } else {
          rowData.value = [];
          console.warn("  :", data);
        }
      } catch (error) {
        console.error("CustomGrid fetch :", error);
      }
      isLoading.value = false;
    };

    const statusOptions = ["", " ", "", "", " "];
    const selectedStatus = ref("");

    return {
      statusOptions,
      selectedStatus,
      isLoading,
      isLoading1,
      gridKey,
      rowData,
      columnApi,
      onGridReady,
      defaultColDef,
      props,
      onRowSelected,
      selectedOrderId,
      updateOrder,
      reloadData,
      selectedOrderState,
      bank_account,
      cake_price,
    };
  },
};
</script>

